import { ActionTypes } from "../constants/action-types";

const initialState = {
  products: [],
};
//takes the state and the action
export const productsReducer = (state = initialState, { type, payload }) => {
  switch (type) {
    case ActionTypes.SET_PRODUCTS:
      return { ...state, products: payload };
    default:
      return state;
  }
};

export const selectedProductsReducer = (state = {}, { type, payload }) => {
  console.log(type);
  switch (type) {
    case ActionTypes.SELECTED_PRODUCT:
      return { ...state, ...payload };
    case ActionTypes.REMOVE_SELECTED_PRODUCT:
      return {};
    default:
      return state;
  }
};

export const selectedProductTypeReducer = (
  state = { type: "mug" },
  { type, payload }
) => {
  console.log(type);
  switch (type) {
    case ActionTypes.SELECTED_PRODUCT_TYPE:
      return { ...state, ...payload };
    default:
      return state;
  }
};

export const filteredProductsReducer = (
  state = initialState,
  { type, payload }
) => {
  switch (type) {
    case ActionTypes.SET_FILTERED_PRODUCTS:
      return { ...state, products: payload };
    default:
      return state;
  }
};
